.calendar {
  margin-bottom: 20px;
  border-radius: 12px;
  padding: 10px;
  background-color: #fff;
  border: none;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease, opacity 0.3s ease;
  animation: fadeInUp 0.6s ease;
}

.react-calendar {
  width: 100% !important;
  max-width: 500px;
  font-size: 1.2rem;
  padding: 20px;
  border: none;
}

/* ✅ 캘린더 타일 높이 줄임 */
.react-calendar__tile {
  height: 100px !important;
}

/* 작은 화면일 때 더 줄임 */
@media (max-width: 960px) {
  .react-calendar__tile {
    height: 80px !important;
  }
}

.react-calendar__navigation button {
  font-size: 1.5rem;
  font-weight: bold;
}

.react-calendar__month-view__weekdays {
  font-size: 1.2rem;
  font-weight: 600;
}

.time-slots {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  justify-content: center;
  margin-top: 0;
}

/* ✅ 시간 선택 영역 높이 제한 + 스크롤 */
.time-slot-list {
  display: flex;
  flex-wrap: wrap;
  max-height: 240px;
  overflow-y: auto;
  justify-content: center;
}

.time-slot {
  transition: 0.2s;
  width: 70px;
  padding: 14px;
  margin: 6px;
  font-size: 15px;
  border-radius: 5px;
  border: 1px solid #ccc;
}

.time-slot:hover {
  opacity: 0.8;
}

.select-time-label {
  font-size: 24px;
  font-weight: bold;
  text-align: center;
  margin-top: 0;
}

.time-section {
  transition: transform 0.3s ease, opacity 0.3s ease;
  animation: fadeInUp 0.6s ease;
}

form {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

label {
  font-weight: bold;
  margin-top: 8px;
}

input, textarea {
  padding: 10px;
  font-size: 14px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

textarea {
  resize: vertical;
  height: 60px;
}

.next-button {
  width: 80px;
  padding: 10px;
  background-color: #94c4e0;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  margin-top: 12px;
  font-weight: bold;
  font-size: 15px;
}

.next-button:hover {
  background-color: #70a4c2;
}

/* 전체 스크롤 안정화 */
html, body {
  margin: 0;
  padding: 0;
  overflow-y: auto;
  height: 100%;
}
