.contact-container {
  transition: transform 0.3s ease, opacity 0.3s ease;
  animation: fadeInUp 0.6s ease;
  margin-top: 50px;
  padding: 20px;
  background-color: #fff;
  text-align: center;
}

.contact-divider {
  height: 3px;
  background-color: #94c4e0;
  margin-bottom: 20px;
}

.contact-content p {
  margin-top: 30px;
  margin: 10px 0;
  color: #333;
  font-size: 14px;
}
