/* 레이아웃 전체 */
.home-layout {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 40px;
  padding: 40px;
  align-items: start;
}

/* 왼쪽 캘린더 영역 */
.home-left {
  display: flex;
  flex-direction: column;
  align-items: center;
}

/* 오른쪽 시간+폼 영역 */
.home-right {
  display: flex;
  flex-direction: column;
  gap: 20px;
  max-height: 100vh;
  overflow-y: auto;
}

/* 폼 컨테이너 */
.home-form-container {
  width: 100%;
  max-width: 650px;
  padding: 24px;
  border-radius: 12px;
  background-color: white;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
  overflow-y: auto;
  max-height: 400px;
}

/* 버튼 스타일 */
.res-button {
  width: 100%;
  padding: 12px;
  background-color: #94c4e0;
  color: white;
  border: none;
  border-radius: 6px;
  font-weight: bold;
  font-size: 16px;
  margin-top: 12px;
  cursor: pointer;
}

/* 반응형 레이아웃 */
@media (max-width: 960px) {
  .home-layout {
    grid-template-columns: 1fr;
    padding: 20px;
  }

  .home-left,
  .home-right {
    align-items: center;
    justify-content: center;
  }

  .home-form-container {
    max-width: 90%;
    padding: 20px;
    max-height: none;
  }

  .time-slot {
    width: 65px;
    padding: 10px;
    font-size: 14px;
  }

  .time-slot-list {
    max-height: 200px;
  }
}
